export function preventDefault(this: any) {
  if (this.nativeEvent) {
    //react
    // https://github.com/facebook/react/issues/6436
    // Chrome 73window,document,bodyscroll,wheel,mobile touchpassivetrue
    // e.preventDefault()Unable to preventDefault inside passive event listener invocation.
    // reactdocumentdocumentpassivetruereactpassive
    // e.preventDefault()e.nativeEvent.preventDefault()
    return;
  } else {
    if (!this.preventDefault) {
      this.returnValue = false;
    } else {
      this.preventDefault();
    }
  }
}

export function stopPropagation(this: any) {
  if (!this.stopPropagation) {
    this.cancelBubble = true;
  } else {
    this.stopPropagation();
  }
}

export function stopImmediatePropagation(this: any) {
  if (this.nativeEvent) {
    //react
    // reactstopImmediatePropagation
    // e.nativeEvent.stopImmediatePropagation()document
    if (this.nativeEvent.stopImmediatePropagation) {
      this.nativeEvent.stopImmediatePropagation();
    }
  } else {
    if (!this.stopImmediatePropagation) {
      stopPropagation.apply(this, []);
    } else {
      this.stopImmediatePropagation();
    }
  }
}
